import React from "react";
import {Link} from "react-router-dom";

class Home extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            growText: true,
            imgx: "640px",
            imgy: "480px"
        }
    }

    handleClick() {
        this.setState(state => ({
            imgx: this.state.growText ? '320px' : '640px',
            imgy: this.state.growText ? '240px' : '480px',
            growText: !state.growText
        }));
    }

    render() {
        return (
            <div className="box">
                <h2>Larong ginawa ko saa RPG Maker</h2>
                <div>
                    <Link to={'/rpg'}>
                        <div className="button">
                            Play Game!
                        </div>
                    </Link>
                    <div className={"button"}
                    onClick={() =>(this.handleClick())}>
                        {this.state.growText ? 'Shrink Gif' : 'Grow Gif'}
                    </div>
                </div>
                <div>
                    <img alt={"RPG Gif"} src= {require('./assets/ataks.gif')} id="gif" width={this.state.imgx} height={this.state.imgy}/>
                </div>
            </div>
        )
    }
}
export default Home;